.i {
  display: flex;
  height: 100vh;
}

.i-left {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.i-right {
  flex: 1 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

