}

.i-scroll {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 20px;
}

.i-img {
  width: 100%;
  height: 60%;
  margin-top: 100px;
  position: absolute;
}

.btn-highlighted-btn {
  margin-left: 50px;
  width: 150px;
  border-radius: 50px;
  outline: none;
  border: solid 2px #ff4500;
  background: #ff4500;

  color: white;
  transition: all 0.3s linear;
}

.btn-highlighted-btn:hover,
.btn-highlighted-btn:focus {
  background: #c23502;
  box-shadow: rgba(17, 17, 26, 0.1) 0px 8px 24px,
    rgba(17, 17, 26, 0.1) 0px 16px 56px, rgba(17, 17, 26, 0.1) 0px 24px 80px;
}

.btn-primary-btn {
  width: 150px;
  border-radius: 50px;
  background: #ff4500;
  transition: all 0.3s linear;
}

.btn-primary-btn:hover,
.btn-primary-btn:focus {
  background: #c23502;
}

